piedra) y óleum (aceite), aceite de roca y se utilizó también en latín. El petróleo se formó a partir de los restos de organismos marinos antiguos, como plantas, algas y bacterias, sepultados por el movimiento de placas de la corteza terrestre y sometida durante millones de años a altas temperaturas y muy altas presiones, los compuestos que formaban a estos seres vivos se transformaron en fósiles orgánicos ricos en carbono e hidrógeno. Se definen como hidrocarburos a los compuestos que están constituidos principalmente por carbono (C) e hidrógeno (H). No debe confundirse con los carbohidratos, aunque las palabras se parecen, estos últimos contienen oxígeno en su fórmula.")]),_c('p',[_vm._v("En muchos lugares del mundo se encontró desde la antigüedad que, como resultado de fracturas en la corteza terrestre, aparecían derrames de un líquido viscoso negro que podía inflamarse y servir en algunas lámparas de aceite. ")]),_c('p',{staticClass:"mb-4"},[_vm._v("Físicamente, el petróleo crudo es un líquido viscoso generalmente de color negro y en ocasiones amarillo. Coloquialmente se le conoce como “el oro negro”. El petróleo crudo es una mezcla heterogénea y compleja de compuestos orgánicos: hay gases, líquidos y sólidos, la mayoría insolubles en agua. Adicionalmente, el petróleo crudo contiene en menor cantidad algunos compuestos que contienen: oxígeno (O), nitrógeno (N), azufre (S) y metales como el sodio (Na), vanadio (V), níquel (Ni), hierro (Fe), cobre (Cu). En la industria, estas impurezas deben ser eliminadas durante el proceso de refinación para obtener productos derivados de mayor calidad.")])]),_c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido"},[_c('h5',[_vm._v("Figura 4.")]),_c('span',[_vm._v(" Composición promedio del crudo extraído del subsuelo")])]),_c('figure',[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/Tema1/fig4.sv