ar. Su combustión produce menos emisiones que el carbón y el petróleo, pero sigue siendo una fuente no renovable. En su estado puro, se compone en su mayoría de metano (CH₄), pero también contiene pequeñas cantidades de etano (C₂H₆), propano (C₃H₈), butano (C₄H₁₀) o pentano (C₅H₁₂) y otros componentes.")]),_c('p',{staticClass:"mb-4"},[_vm._v("Suele tener un 1 % de impurezas; el dióxido de carbono, oxígeno, helio, vapor de agua, etc., todos de combustión limpia.")])]),_c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido"},[_c('h5',[_vm._v("Figura 6.")]),_c('span',[_vm._v(" Composición del gas natural")])]),_c('figure',[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/Tema1/fig6.svg"),"alt":"Gráfico que muestra los componentes del gas natural en su forma primaria, destacando el metano como principal, seguido de etano, propano, butano, pentanos y compuestos no hidrocarburos."}}),_c('figcaption',{staticClass:"des"},[_vm._v("Fuente: https://ovacen.com/gas-natural/")])])])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Energía nuclear"}},[_c('div',{staticClass:"col-md-11 mb-4 mb-md-0"},[_c('p',[_vm._v("La energía nuclear aprovecha la energía contenida en el núcleo de los átomos. En las centrales nucleares actuales, esta energía se libera principalmente a través de la fisión nuclear, un proceso en el que núcleos de átomos pesados, como el uranio - 235, son bombardeados con neutrones, lo que provoca que se dividan en núcleos más pequeños, liberando una gran cantidad de energía en forma de calor y más neutrones, perpetuando una reacción en cadena controlada.")]),_c('p',[_vm._v("El calor generado por esta fisión se utiliza para calentar agua y producir vapor a alta presión. Este vapor acciona turbinas conectadas a generadores eléctricos, transformando la energía térmica en energía mecánica y finalmente en electricidad.")]),_c('p',{staticClass:"mb-4"},[_vm._v("Aunque no emite gases de efecto invernadero durante su operación, la energía nuclear plantea desafíos en cuanto a la gestión de los residuos radiactivos y los riesgos de accidentes nucleares. Además, el uranio utilizado como combustible es un recurso no renovable. Se investiga activamente la fusión nuclear, un proceso que une núcleos atómicos ligeros para liberar energía (como ocurre en el sol), como una posible fuente de energía nuclear más limpia y abundante en el futuro, aunque aún se encuentra en fase experimental.")])]),_c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido"},[_c('h5',[_vm._v("Figura 7.")]),_c('span',[_vm._v(" Energía nuclear")])]),_c('figure',[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/Tema1/fig7.svg"),"alt":"Diagrama ilustrativo del funcionamiento de una central nuclear. Se muestra el proceso desde la fisión del combustible en la vasija del reactor, la generación de vapor que impulsa una turbina conectada a un alternador, y la producción de electricidad. Incluye componentes como el edificio de contención, generadores de vapor, condensador, torre de refrigeración, transformador y sistema de recirculación de agua desde una fuente fría."}}),_c('figcaption',{staticClass:"des"},[_vm._v("Fuente: https://www.foronuclear.org/actualidad/a-fondo/cuales-son-los-distintos-componentes-de-una-central-nuclear/")])])])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Energía hidroeléctrica"}},[_c('div',{staticClass:"col-md-11 mb-4 mb-md-0"},[_c('p',[_vm._v("Aprovecha la energía potencial del agua almacenada en embalses o el flujo de los ríos para generar electricidad mediante turbinas. Es una fuente renovable y de bajas emisiones durante su operación, pero la construcción de grandes represas puede tener impactos ambientales y sociales significativos.")]),_c('p',[_vm._v("Aunque la energía hidroeléctrica utiliza el flujo del agua, un recurso que se renueva naturalmente a través del ciclo hidrológico, no siempre se considera estrictamente renovable debido a los impactos ambientales y sociales significativos que pueden generar las grandes represas. ")]),_c('p',{staticClass:"mb-4"},[_vm._v("Estos impactos incluyen la alteración de ecosistemas fluviales, la sedimentación, el desplazamiento de comunidades y la potencial liberación de gases de efecto invernadero (como el metano) proveniente de la descomposición de materia orgánica en los embalses. Por lo tanto, su sostenibilidad a largo plazo es un tema de debate y depende en gran medida del diseño, la escala y la gestión de los proyectos hidroeléctricos.")])]),_c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido"},[_c('h5',[_vm._v("Figura 8.")]),_c('span',[_vm._v(" Esquema básico de una central hidroeléctrica")])]),_c('figure',[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/Tema1/fig8.svg"),"alt":"Ilustración esquemática del funcionamiento de una central hidroeléctrica. El agua almacenada en un embalse pasa por una compuerta y fluye a través de una tubería de paso, accionando una turbina conectada a un generador eléctrico. La electricidad generada es transportada por torres de tendido eléctrico. El agua utilizada es finalmente liberada por el desagüe de la presa."}}),_c('figcaption',{staticClass:"des"},[_vm._v("Fuente: https://www.ormazabal.com/energia-hidroelectrica-que-es-y-como-funciona/")])])])])])])],1)]),_vm._m(10),_vm._m(11),_vm._m(12),_vm._m(13),_vm._m(14),_c('div',{staticClass:"row justify-content-center align-items-center mb-4",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"col-lg-12"},[_c('TabsC',{staticClass:"color-acento-contenido"},[_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Fotovoltaica"}},[_c('div',{stati