lor. Se utilizan colectores solares térmicos (paneles solares térmicos) para absorber la energía del sol y transferir este calor a un fluido, que puede ser agua, aire u otro fluido caloportador. Este fluido caliente se utiliza directamente para diversas aplicaciones como el calentamiento de agua sanitaria, la calefacción de espacios, el calentamiento de piscinas o incluso para procesos industriales que requieren calor. En sistemas de mayor escala, la energía solar térmica también se puede utilizar para generar electricidad en plantas termosolares, donde el calor solar se concentra para producir vapor que acciona turbinas.")])]),_c('div',{staticClass:"col-md-5 mb-4 mb-md-0"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido"},[_c('h5',[_vm._v("Figura 11.")]),_c('span',[_vm._v(" Funcionamiento de un colector solar térmico")])]),_c('figure',[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/Tema1/fig11.svg"),"alt":"Diagrama de un sistema de energía solar térmica que muestra el funcionamiento de un colector solar. El panel solar calienta un fluido (glicol) en un circuito cerrado, que transfiere el calor a un acumulador. El sistema permite calentar agua fría que entra por una tubería y sale como agua caliente para su uso doméstico."}})]),_c('figcaption',{staticClass:"des"},[_vm._v("Fuente: https://www.solarlinkers.com/tipos-de-energia-solar-termica/")])])])])])],1)]),_c('p',{staticClass:"mb-4"},[_vm._v("La energía solar puede ser aprovechada de dos formas principales: la fotovoltaica y la térmica. La energía solar fotovoltaica convierte la luz del sol en electricidad mediante paneles solares, siendo ideal para alimentar dispositivos eléctricos y sistemas conectados a la red o aislados. En cambio, la energía solar térmica utiliza el calor del sol para calentar agua, comúnmente empleada en usos domésticos e industriales. Mientras que la primera se enfoca en la generación de electricidad, la segunda se orienta al aprovechamiento térmico, lo que per